// 系统设置 === 清除数据
const ClearThirdPlaData = () => import('@/components/SysSetting/ClearThirdPlaData')
// 系统设置 === 系统日志
const SysLog = () => import('@/components/SysSetting/SysLog')
// 系统设置 === ApkMgt版本管理
const ApkMgt = () => import('@/components/SysSetting/ApkMgt')
// 系统设置 === ApkMgt版本管理 添加
const AddApk = () => import('@/components/SysSetting/ApkMgt/AddApk')
// 系统设置 === ApkMgt版本管理 编辑
const EditApk = () => import('@/components/SysSetting/ApkMgt/EditApk')

// 系统设置 === 用户反馈
const Suggest = () => import('@/components/SysSetting/Suggest')
// 清除Redis缓存数据 === 清除系统日志  该权限仅供技术使用
const ClearData = () => import('@/components/SysSetting/ClearData')

const sysSettingRouters = [
  {
    path: '/cl_data',
    name: '清除三方数据',
    meta: {
      title: '清除三方数据',
      scrollToTop: true,
      breadcrumbList: [
        {name: '系统设置', path: ''},
        {name: '清除三方数据', path: ''}
      ]
    },
    component: ClearThirdPlaData
  },
  {
    path: '/sys_log',
    name: '系统日志',
    meta: {
      title: '系统日志',
      scrollToTop: true,
      breadcrumbList: [
        {name: '系统设置', path: ''},
        {name: '系统日志', path: ''}
      ]
    },
    component: SysLog
  },
  {
    path: '/apk',
    name: 'Apk版本管理',
    meta: {
      title: 'Apk版本管理',
      scrollToTop: true,
      breadcrumbList: [
        {name: '系统设置', path: ''},
        {name: 'Apk版本管理', path: ''}
      ]
    },
    component: ApkMgt
  },
  {
    path: '/apk/add',
    name: '添加Apk版本',
    meta: {
      title: '添加Apk版本',
      scrollToTop: true,
      breadcrumbList: [
        {name: '系统设置', path: ''},
        {name: 'Apk版本管理', path: '/apk'},
        {name: '添加Apk版本', path: ''}
      ]
    },
    component: AddApk
  },
  {
    path: '/apk/edit',
    name: '编辑Apk版本',
    meta: {
      title: '编辑Apk版本',
      scrollToTop: true,
      breadcrumbList: [
        {name: '系统设置', path: ''},
        {name: 'Apk版本管理', path: '/apk'},
        {name: '编辑Apk版本', path: ''}
      ]
    },
    component: EditApk
  },
  {
    path: '/suggest',
    name: '用户反馈',
    meta: {
      title: '用户反馈',
      scrollToTop: true,
      breadcrumbList: [
        {name: '系统设置', path: ''},
        {name: '用户反馈', path: ''}
      ]
    },
    component: Suggest
  },
  {
    path: '/clear',
    name: '清除Redis缓存',
    meta: {
      title: '清除Redis缓存',
      scrollToTop: true,
      breadcrumbList: [
        {name: '系统设置', path: ''},
        {name: '清除Redis缓存', path: ''}
      ]
    },
    component: ClearData
  }
]

export default {
  sysSettingRouters
}




