// 商城配置==》预售商品推荐
const PreProductsFea = () => import('@/components/MallConfig/PreProductsFea')
// 商城配置==》设置
const SetFreight = () => import('@/components/MallConfig/SetFreight')
// banner管理
const BannerMgt = () => import('@/components/MallConfig/BannerMgt')
// 新增banner
const AddBanner = () => import('@/components/MallConfig/BannerMgt/AddBanner')
// 编辑banner
const EditBanner = () => import('@/components/MallConfig/BannerMgt/EditBanner')
// 分组管理
const GroupMgt = () => import('@/components/MallConfig/GroupMgt')
// 分组商品管理
const MgGroupProducts = () => import('@/components/MallConfig/GroupMgt/MgGroupProducts')

const mallConfigRouters = [
  {
    path: '/set_page',
    name: '页面管理',
    meta: {
      title: '页面管理',
      scrollToTop: true,
      hideSelectCity: true,
      breadcrumbList: [
        {name: '商城配置', path: ''},
        {name: '页面管理', path: ''}
      ]
    },
    component: () => import('@/components/MallConfig/PageMgt')
  },
  // 页面类型：1-首页；2-专题页
  {
    path: '/set_page/add/:pageType/:tempId',
    name: '添加页面',
    meta: {
      title: '添加页面',
      scrollToTop: true,
      hideSelectCity: true,
      breadcrumbList: [
        {name: '商城配置', path: ''},
        {name: '页面管理', path: '/set_page'},
        {name: '添加页面', path: ''}
      ]
    },
    component: () => import('@/components/MallConfig/PageMgt/AddPage')
  },
  {
    path: '/set_page/edit/:pageType/:tempId/:pageId',
    name: '编辑页面',
    meta: {
      title: '编辑页面',
      scrollToTop: true,
      hideSelectCity: true,
      breadcrumbList: [
        {name: '商城配置', path: ''},
        {name: '页面管理', path: '/set_page'},
        {name: '编辑页面', path: ''}
      ]
    },
    component: () => import('@/components/MallConfig/PageMgt/EditPage')
  },
  {
    path: '/tp_feature',
    name: '商品推荐',
    meta: {
      title: '商品推荐',
      scrollToTop: true,
      hideSelectCity: true,
      breadcrumbList: [
        {name: '商城配置', path: ''},
        {name: '商品推荐', path: ''}
      ]
    },
    component: () => import('@/components/MallConfig/TimeProductsFea')
  },
  {
    path: '/prep_feature',
    name: '预售商品推荐',
    meta: {
      title: '预售商品推荐',
      scrollToTop: true,
      breadcrumbList: [
        {name: '商城配置', path: ''},
        {name: '预售商品推荐', path: ''}
      ]
    },
    component: PreProductsFea
  },
  {
    path: '/set_freight',
    name: '设置',
    meta: {
      title: '设置',
      scrollToTop: true,
      breadcrumbList: [
        {name: '商城配置', path: ''},
        {name: '设置', path: ''}
      ]
    },
    component: SetFreight
  },
  {
    path: '/banner_mgt',
    name: '预售Banner管理',
    meta: {
      title: '预售Banner管理',
      scrollToTop: true,
      breadcrumbList: [
        {name: '商城配置', path: ''},
        {name: '预售Banner管理', path: ''}
      ]
    },
    component: BannerMgt
  },
  {
    path: '/banner_mgt/add_banner',
    name: '新增Banner',
    meta: {
      title: '新增Banner',
      scrollToTop: true,
      breadcrumbList: [
        {name: '商城配置', path: ''},
        {name: '预售Banner管理', path: '/banner_mgt'},
        {name: '新增Banner', path: ''}
      ]
    },
    component: AddBanner
  },
  {
    path: '/banner_mgt/edit_banner',
    name: '编辑Banner',
    meta: {
      title: '编辑Banner',
      scrollToTop: true,
      breadcrumbList: [
        {name: '商城配置', path: ''},
        {name: '预售Banner管理', path: '/banner_mgt'},
        {name: '编辑Banner', path: ''}
      ]
    },
    component: EditBanner
  },
  {
    path: '/group_mgt',
    name: '预售分组管理',
    meta: {
      title: '预售分组管理',
      scrollToTop: true,
      breadcrumbList: [
        {name: '商城配置', path: ''},
        {name: '预售分组管理', path: ''}
      ]
    },
    component: GroupMgt
  },
  {
    path: '/group_mgt/mg_groupProducts',
    name: '预售分组商品管理',
    meta: {
      title: '预售分组商品管理',
      scrollToTop: true,
      breadcrumbList: [
        {name: '商城配置', path: ''},
        {name: '预售分组管理', path: '/group_mgt'},
        {name: '预售分组商品管理', path: ''}
      ]
    },
    component: MgGroupProducts
  },
  {
    path: '/store_page',
    name: '门店页面管理',
    meta: {
      title: '门店页面管理',
      scrollToTop: true,
      hideSelectCity: true,
      breadcrumbList: [
        {name: '商城配置', path: ''},
        {name: '门店页面管理', path: ''}
      ]
    },
    component: () => import('@/components/MallConfig/StorePageMgt')
  },
  {
    path: '/ad',
    name: '首页弹窗广告',
    meta: {
      title: '首页弹窗广告',
      scrollToTop: true,
      hideSelectCity: true,
      breadcrumbList: [
        {name: '商城配置', path: ''},
        {name: '首页弹窗广告', path: ''}
      ]
    },
    component: () => import('@/components/MallConfig/Ad')
  },
  {
    path: '/ad/add',
    name: '新增首页弹窗广告',
    meta: {
      title: '新增首页弹窗广告',
      scrollToTop: true,
      hideSelectCity: true,
      breadcrumbList: [
        {name: '商城配置', path: ''},
        {name: '首页弹窗广告', path: '/ad'},
        {name: '新增首页弹窗广告', path: ''}
      ]
    },
    component: () => import('@/components/MallConfig/Ad/AddPage')
  },
  {
    path: '/ad/edit/:id/:type',
    name: '编辑弹窗广告',
    meta: {
      title: '编辑弹窗广告',
      scrollToTop: true,
      hideSelectCity: true,
      breadcrumbList: []
    },
    component: () => import('@/components/MallConfig/Ad/EditPage')
  },
  {
    path: '/ad/show/:id',
    name: '查看弹窗广告',
    meta: {
      title: '查看弹窗广告',
      scrollToTop: true,
      hideSelectCity: true,
      breadcrumbList: [
        {name: '商城配置', path: ''},
        {name: '首页弹窗广告', path: '/ad'},
        {name: '查看弹窗广告', path: ''}
      ]
    },
    component: () => import('@/components/MallConfig/Ad/ShowPage')
  }
]
export default {
  mallConfigRouters
}



