import config from '@/utils/config'
// 设置baseUrl
let baseUrl = config.reqUrl
// 登录相关接口
const commonUrl = {
  queryMenu: "/foundation/user/func/list",
  getVerificationImage: baseUrl + '/basal/adminUser/getVerificationImage', // 获取验证码
  token: baseUrl + '/authorization/login', // 登录
  getQiNiuToken: '/foundation/qiniu/auth/file',
  // 查询用户区域店组一级列表
  userAreaStoreGroupList: '/foundation/storeGroup/userAreaStoreGroupList',
  // 查询ERP通用配置的共通资料数据
  listByCommonCodes: '/foundation/businessConfig/listByCommonCodes',
  // 生成二维码
  generateQRCode: '/member/a/qrScanCode/generateQRCode'
}
export default commonUrl



