// 门店管理
// 商城门店
const BlxStore = () => import('@/components/StoreMgt/BlxStore')
// 饿百门店
const EleStore = () => import('@/components/StoreMgt/EleStore')
// 京东门店
const JdStore = () => import('@/components/StoreMgt/JdStore')
// 美团门店
const MtStore = () => import('@/components/StoreMgt/MtStore')
// 本来鲜开启线上业务
const OpenOnlineJob = () => import('@/components/StoreMgt/OpenOnlineJob')
// 配送公司设置
const BlxDelivery = () => import('@/components/StoreMgt/BlxDelivery')
// 配送公司设置 -- 添加
const BlxDeliveryAdd = () => import('@/components/StoreMgt/BlxDelivery/AddPage')
// 配送公司设置 -- 编辑
const BlxDeliveryEdit = () => import('@/components/StoreMgt/BlxDelivery/EditPage')
// 顺丰门店
const SfStore = () => import('@/components/StoreMgt/SfStore')
// 大屏展示配置
const ScreenSet = () => import('@/components/StoreMgt/ScreenSet')
// 添加大屏展示配置
const ScreenSetAdd = () => import('@/components/StoreMgt/ScreenSet/AddPage')
// 编辑大屏展示配置
const ScreenSetEdit = () => import('@/components/StoreMgt/ScreenSet/editPage')
// 门店配送方式
const StoreDelivery = () => import('@/components/StoreMgt/StoreDelivery')
const storeMgtRouters = [
  {
    path: '/blx_store',
    name: '商城门店',
    meta: {
      title: '商城门店',
      scrollToTop: true,
      breadcrumbList: [
        {name: '店铺设置', path: ''},
        {name: '商城门店', path: ''}
      ]
    },
    component: BlxStore
  },
  {
    path: '/mt_store',
    name: '美团门店',
    meta: {
      title: '美团门店',
      scrollToTop: true,
      breadcrumbList: [
        {name: '店铺设置', path: ''},
        {name: '美团门店', path: ''}
      ]
    },
    component: MtStore
  },
  {
    path: '/jd_store',
    name: '京东门店',
    meta: {
      title: '京东门店',
      scrollToTop: true,
      breadcrumbList: [
        {name: '店铺设置', path: ''},
        {name: '京东门店', path: ''}
      ]
    },
    component: JdStore
  },
  {
    path: '/ele_store',
    name: '饿百门店',
    meta: {
      title: '饿百门店',
      scrollToTop: true,
      breadcrumbList: [
        {name: '店铺设置', path: ''},
        {name: '饿百门店', path: ''}
      ]
    },
    component: EleStore
  },
  // platformId: 平台id  storeNo：门店编号 storeId: 门店id
  {
    path: '/blx_store/o_ol_job/:platformId/:storeNo/:storeId',
    name: '编辑门店',
    meta: {
      title: '编辑门店',
      scrollToTop: true,
      breadcrumbList: [
        {name: '店铺设置', path: ''},
        {name: '商城门店', path: '/blx_store'},
        {name: '编辑门店', path: ''}
      ]
    },
    component: OpenOnlineJob
  },
  {
    path: '/blx_delivery',
    name: '配送公司设置',
    meta: {
      title: '配送公司设置',
      scrollToTop: true,
      breadcrumbList: [
        {name: '店铺设置', path: ''},
        {name: '配送公司设置', path: ''}
      ]
    },
    component: BlxDelivery
  },
  {
    path: '/blx_delivery/add',
    name: '添加门店配送',
    meta: {
      title: '配送公司设置',
      scrollToTop: true,
      breadcrumbList: [
        {name: '店铺设置', path: ''},
        {name: '配送公司设置', path: '/blx_delivery'},
        {name: '添加门店配送', path: ''}
      ]
    },
    component: BlxDeliveryAdd
  },
  {
    path: '/blx_delivery/edit/:id/:storeNo',
    name: '编辑门店配送',
    meta: {
      title: '配送公司设置',
      scrollToTop: true,
      breadcrumbList: [
        {name: '店铺设置', path: ''},
        {name: '配送公司设置', path: '/blx_delivery'},
        {name: '编辑门店配送', path: ''}
      ]
    },
    component: BlxDeliveryEdit
  },
  {
    path: '/sf_store',
    name: '顺丰门店',
    meta: {
      title: '顺丰门店',
      scrollToTop: true,
      breadcrumbList: [
        {name: '店铺设置', path: ''},
        {name: '顺丰门店', path: ''}
      ]
    },
    component: SfStore
  },
  {
    path: '/sc861816',
    name: '大屏展示配置',
    meta: {
      title: '大屏展示配置',
      scrollToTop: true,
      hideSelectCity: true,
      breadcrumbList: [
        {name: '店铺设置', path: ''},
        {name: '大屏展示配置', path: ''}
      ]
    },
    component: ScreenSet 
  },
  {
    path: '/sc861816/add',
    name: '新增模板',
    meta: {
      title: '新增模板',
      scrollToTop: true,
      hideSelectCity: true,
      breadcrumbList: [
        {name: '店铺设置', path: ''},
        {name: '大屏展示配置', path: '/sc861816'},
        {name: '新增模板', path: ''}
      ]
    },
    component: ScreenSetAdd 
  },
  {
    path: '/sc861816/edit/:id',
    name: '编辑模板',
    meta: {
      title: '编辑模板',
      scrollToTop: true,
      hideSelectCity: true,
      breadcrumbList: [
        {name: '店铺设置', path: ''},
        {name: '大屏展示配置', path: '/sc861816'},
        {name: '编辑模板', path: ''}
      ]
    },
    component: ScreenSetEdit 
  },
  {
    path: '/store_delivery',
    name: '门店配送方式',
    meta: {
      title: '门店配送方式',
      scrollToTop: true,
      breadcrumbList: [
        {name: '店铺设置', path: ''},
        {name: '门店配送方式', path: ''}
      ]
    },
    component: StoreDelivery 
  },
  {
    path: '/poster',
    name: '团长营销海报配置',
    meta: {
      title: '团长营销海报配置',
      scrollToTop: true,
      hideSelectCity: true,
      breadcrumbList: [
        {name: '店铺设置', path: ''},
        {name: '团长营销海报配置', path: ''}
      ]
    },
    component: () => import('@/components/StoreMgt/Poster') 
  },
  {
    path: '/poster/add',
    name: '添加模板',
    meta: {
      title: '添加模板',
      scrollToTop: true,
      hideSelectCity: true,
      breadcrumbList: [
        {name: '店铺设置', path: ''},
        {name: '团长营销海报配置', path: '/poster'},
        {name: '添加模板', path: ''}
      ]
    },
    component: () => import('@/components/StoreMgt/Poster/Add') 
  },
  {
    path: '/poster/edit/:id',
    name: '编辑模板',
    meta: {
      title: '编辑模板',
      scrollToTop: true,
      hideSelectCity: true,
      breadcrumbList: [
        {name: '店铺设置', path: ''},
        {name: '团长营销海报配置', path: '/poster'},
        {name: '编辑模板', path: ''}
      ]
    },
    component: () => import('@/components/StoreMgt/Poster/Edit') 
  },
  {
    path: '/media',
    name: '大屏幕多媒体播报',
    meta: {
      title: '大屏幕多媒体播报',
      scrollToTop: true,
      hideSelectCity: true,
      cantCheck: true,
      breadcrumbList: [
        {name: '店铺设置', path: ''},
        {name: '大屏幕多媒体播报', path: ''}
      ]
    },
    component: () => import('@/components/StoreMgt/ScreenSet/Media') 
  },
  {
    path: '/media/add',
    name: '新增模板',
    meta: {
      title: '新增模板',
      scrollToTop: true,
      hideSelectCity: true,
      cantCheck: true,
      breadcrumbList: [
        {name: '店铺设置', path: ''},
        {name: '大屏幕多媒体播报', path: '/media'},
        {name: '新增模板', path: ''}
      ]
    },
    component: () => import('@/components/StoreMgt/ScreenSet/Media/Add') 
  },
  {
    path: '/media/edit/:id',
    name: '编辑模板',
    meta: {
      title: '编辑模板',
      scrollToTop: true,
      hideSelectCity: true,
      breadcrumbList: [
        {name: '店铺设置', path: ''},
        {name: '大屏幕多媒体播报', path: '/media'},
        {name: '编辑模板', path: ''}
      ]
    },
    component: () => import('@/components/StoreMgt/ScreenSet/Media/Edit') 
  },
  {
    path: '/set_dis',
    name: '接龙折扣配置',
    meta: {
      title: '接龙折扣配置',
      scrollToTop: true,
      hideSelectCity: true,
      cantCheck: true,
      breadcrumbList: [
        {name: '店铺设置', path: ''},
        {name: '接龙折扣配置', path: ''}
      ]
    },
    component: () => import('@/components/StoreMgt/SetDis') 
  }
]

export default {
  storeMgtRouters
}




