// 会员基础数据
const MemberData = () => import('@/components/Report/MemberData')
// 财务明细报表
const FinData = () => import('@/components/Report/FinData')

const reportRouter = [
  {
    path: '/mdata',
    name: '会员基础数据',
    meta: {
      title: '会员基础数据',
      scrollToTop: true,
      breadcrumbList: [{
        name: '客户营销',
        path: ''
      },
      {
        name: '会员基础数据',
        path: ''
      }
      ]
    },
    component: MemberData
  },
  {
    path: '/findata',
    name: '财务明细报表',
    meta: {
      title: '财务明细报表',
      scrollToTop: true,
      breadcrumbList: [{
        name: '客户营销',
        path: ''
      },
      {
        name: '财务明细报表',
        path: ''
      }
      ]
    },
    component: FinData
  }
]

export default {
  reportRouter
}



